.year-buttons-container {
    display: flex;
    justify-content: center;
  }
  
.year-button {
  display: inline-block;
  padding: 10px 20px;
  margin: 10px;
  font-size: 18px;
  font-weight: bold;
  color: white;
  background-color: pink;
  border: none;
  border-radius: 5px;
  text-decoration: none;
  cursor: none;
}

.year-button:hover {
  background-color: hotpink;
}

.back-button {
  padding: 10px 20px;
  margin: 10px;
  background-color: #FF69B4; /* Pink color */
  color: white;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  font-weight: bold;
  cursor: none;
  text-align: center;
  text-decoration: none;
  display: inline-block
}

.back-button-container {
  display: flex;
  justify-content: center;
}

.back-button:hover {
  background-color: #FF1493; /* Darker pink on hover */
}